import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Grid, Card, Box, Button, CircularProgress } from '@material-ui/core';
import useStyles from './style';
import { NetworkSelector, TransactionSettingsModal, StepViewer, TokenInput } from 'components';
import { RecipientBar, RecipientInput, SendTokenBar, SendTokenInput } from './components';
import { BackIcon, PreviewIcon, SettingIcon } from './icons/icon';
import Web3 from 'web3';
import { confirmCorrectAddress, getBNValue, getCorrectDecValue, gasLimit, gasPrice, getGasTransferToken, transferToken, approveToken } from '../../web3/contracts';
import { CLUTCH_SENDER_ADDRESS, ETH_ADDRESS, MAIN_NETWORK_ID, NETWORK_TX_ENDPOINT, UNISWAP_ADDRESS, USDC_ADDRESS, USDT_ADDRESS, WETH_ADDRESS } from 'utils/constants';
import { getAmountsOut } from 'web3/uniswap';
import { switchNetwork } from 'web3/network';
import { useToasts } from 'react-toast-notifications';
import { useSelector } from 'react-redux';
import { getReducedWalletAddress } from 'utils/services';
import useActiveWeb3React from "hooks/web3-react"
import { useLocation } from 'react-router-dom';

const Send = props => {
  const theme = useTheme();
  const classes = useStyles(theme);
  const networkList = [
    { networkName: 'Ethereum', image: '/images/EthereumLogo.png' },
    { networkName: 'Polygon', image: '/images/Polygon.png' },
    { networkName: 'Arbitrum', image: '/images/ArbitrumLogo.png' }
  ];
  const [selectedNetwork, setSelectedNetwork] = useState({
    networkName: 'Ethereum',
    image: '/images/EthereumLogo.png'
  });
  const [token, setToken] = useState({});
  const [settingsModalOpen, setSettingsModalOpen] = useState(false);
  const [slippage, setSlippage] = useState(0);
  const [speed, setSpeed] = useState(0);
  const [inputedSlippage, setInputedSlippage] = useState('');
  const [usdAmount, setUsdAmount] = useState(0);
  const [tokenAmount, setTokenAmount] = useState(0);
  const [step, setStep] = useState(0);
  const [ethPrice, setEthPrice] = useState(0);
  const [gas, setGas] = useState(0);
  const [gasLimit, setGasLimit] = useState(21000);
  const [hash, setHash] = useState('');
  const [error, setError] = useState(false);
  const [loading, setLoading] = useState(false);
  const currentWallet = useSelector(state => state.app.currentWallet);
  const { account, library } = useActiveWeb3React();
  const { addToast } = useToasts();
  const location = useLocation();
  const [recipient, setRecipient] = useState({address: location?.state?.address || ''});
  const [approveLoading, setApproveLoading] = useState(false);
  const [approveButton, setApproveButton] = useState(false);

  useEffect(() => {
    if (selectedNetwork?.networkName != 'Ethereum') {
      addToast('Clutch is only supporting Ethereum network. Please switch to the Ethereum network.', { appearance: 'error', autoDismissTimeout: 5000, autoDismiss: true })
    }
  }, [selectedNetwork])
  
  useEffect(() => {
    getEthPrice()
  }, [])

  useEffect(() => {
    getGasPrice()
  }, [speed])

  useEffect(() => {
    if (step == 1) {
      estimateGas()
    }
  }, [step])

  useEffect(() => {
    setUsdAmount(0);
    setTokenAmount(0);
  }, [token])

  // useEffect(() => {
  //   if (token.address && token.address != ETH_ADDRESS)
  //     setApproveButton(true)
  //   else
  //     setApproveButton(false)
  // }, [token])

  const getEthPrice = async () => {
    let res = 0;
    const result = await getAmountsOut(UNISWAP_ADDRESS, [WETH_ADDRESS, USDT_ADDRESS], getBNValue(1, 18) )
    if (result.status) {
      res = getCorrectDecValue(result.data[1], 6, -1, false)
    }
    setEthPrice(res)
  }

  const getGasPrice = async () => {
    let res = await gasPrice();
    if (speed == 0)
      setGas(res)
    else if (speed == 1)
      setGas(res * 1.5)
    else
      setGas(res * 2)
  }

  const estimateGas = async () => {
    const res = await getGasTransferToken(token.address, currentWallet, recipient.address, getBNValue(tokenAmount, token.decimals))
    if (res.status) {
      console.log(res.data, gas)
      setGasLimit(res.data)
    }
  }


  const handleChangeToken = async (item) => {
    setToken(item)
  }

  const handleApply = (_slippage, _speed, _inputedSlippage) => {
    setSlippage(_slippage);
    setSpeed(_speed);
    setInputedSlippage(_inputedSlippage)
  }

  const handleChangeAmount = (value, mode) => {
    if (mode == 0) {
      setTokenAmount(value);
      setUsdAmount(value * (token?.price || 0))
    } else {
      setUsdAmount(value)
      setTokenAmount(value / ((!token?.price || token?.price == 0) ? 1 : token?.price))
    }
  }

  const previewSwap = async () => {
    if (!token?.symbol || isNaN(tokenAmount) || tokenAmount == 0 || !confirmCorrectAddress(recipient?.address || '')) {
      addToast('Invalid token, amount, recipient address', { appearance: 'error', autoDismiss: true });
    } else {
      setStep(1)
    }
  }

  const send = async () => {
    if (!currentWallet || currentWallet.length == 0)
      addToast('Please connect wallet.', { appearance: 'error', autoDismiss: true});
    else if (currentWallet != account?.toLowerCase())
      addToast(`Please switch your account to ${currentWallet}`, { appearance: 'error', autoDismiss: true});
    else if (library) {
      setStep(2);
      setLoading(true);
      const net_ret = await switchNetwork(MAIN_NETWORK_ID)
      if (net_ret) {
        const web3 = new Web3(library.provider)
        const result = await transferToken(web3, token?.address, currentWallet, recipient?.address, getBNValue(tokenAmount, token?.decimals), gas, gasLimit)
        if (result.status) {
          setError(false);
          setHash(result.data.hash)
        } else {
          setError(true);
        }
      } else {
        setError(true);
      }

      setLoading(false);
    }
  }

  const handleApprove = async () => {
    if (!currentWallet || currentWallet.length == 0)
      addToast('Please connect wallet.', { appearance: 'error', autoDismiss: true});
    else if (currentWallet != account?.toLowerCase())
      addToast(`Please switch your account to ${currentWallet}`, { appearance: 'error', autoDismiss: true});
    else if (!token?.symbol || !tokenAmount || tokenAmount == 0 || tokenAmount >= getCorrectDecValue(token?.balance || 0, token?.decimals, -1, false)){
      addToast('Invalid token, amount', { appearance: 'error', autoDismiss: true });
    }
    else if (library) {
      setApproveLoading(true);
      setError(false);
      const net_ret = await switchNetwork(MAIN_NETWORK_ID)
      if (net_ret) {
        const web3 = new Web3(library.provider)
        const result = await approveToken(web3, token?.address, getBNValue(tokenAmount, token?.decimals), account, CLUTCH_SENDER_ADDRESS)
        if (result.status) {
          setError(false);
          setHash(result.data.hash)
          setApproveButton(false);
        } else {
          setError(true);
          addToast('Approve failed', { appearance: 'error', autoDismiss: true });
        }
      } else {
        setError(true);
        addToast('Switch network failed', { appearance: 'error', autoDismiss: true });
      }

      setApproveLoading(false);
    }
  }

  return (
    <Box className={classes.root}>
      <Grid container spacing={2}>
        <Grid item lg={2} xs={12}>
          <StepViewer step="STEP 1:" description="Select your network" />
          <NetworkSelector
            list={networkList}
            selectedItem={selectedNetwork}
            setSelectedItem={setSelectedNetwork}
          />
        </Grid>
        <Grid item lg={6} xs={12}>
          {
            step == 0 ?
              <Card className={classes.step_card}>
                <Box className={classes.card_header}>
                  <StepViewer step="STEP 2:" description="Send" />
                  <Box className={classes.settings_container}>
                    <Box className={classes.setting_description}>
                      Settings
                    </Box>
                    <SettingIcon className={classes.setting_icon} onClick={() => setSettingsModalOpen(true)}/>
                  </Box>
                </Box>
                <SendTokenInput tokenInfo={token} handleChange={handleChangeToken} />
                <Box className={classes.balance}>
                  {token?.symbol && `${token?.symbol} balance: ${getCorrectDecValue(token?.balance || 0, token?.decimals, 3)} ($${getCorrectDecValue((token?.balance || 0) * (token?.price || 0), token?.decimals, 1) })` }
                </Box>
                <Box className={classes.tokenInput}>
                  <TokenInput 
                    value={tokenAmount} 
                    onChange={(e) => handleChangeAmount(e.target.value, 0)}
                    icon={token?.logoURI ? <img src={token?.logoURI} width={20} height={20}/> : '?'}/>
                  <Box style={{marginLeft: 14, marginRight: 14}}>=</Box>
                  <TokenInput 
                    value={usdAmount}
                    onChange={(e) => handleChangeAmount(e.target.value, 1)}
                    icon="$"/>
                </Box>
                <RecipientInput recipientInfo={recipient} handleChange={setRecipient} />
                <Button 
                  variant="contained"
                  className={classes.preview_button}
                  onClick={() => previewSwap()}
                  disabled={!token?.symbol || isNaN(tokenAmount) || tokenAmount == 0 || !confirmCorrectAddress(recipient?.address || '')}
                  >
                    Preview Send
                    <PreviewIcon className={classes.preview_icon}/>
                </Button>
              </Card>
            :
            step == 1 ?
              <Card className={classes.step_card}>
                <Box className={classes.back} onClick={() => setStep(0)}>
                  <BackIcon style={{marginRight: 12}}/>
                  Back
                </Box>
                <Box className={classes.title}>
                  Preview Send
                </Box>
                <SendTokenBar tokenInfo={token}  usdAmount={usdAmount} tokenAmount={tokenAmount}/>
                <RecipientBar recipientInfo={recipient} />
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 18}}>
                  <Box className={classes.sub}>Network Fee:</Box>
                  <Box className={classes.info}>{`${getCorrectDecValue(gas * gasLimit, 18, 5)} ($${getCorrectDecValue(gas * gasLimit * ethPrice, 18, 2)})`}</Box>
                </Box>
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 10}}>
                  <Box className={classes.sub}>Slippage tolerance:</Box>
                  <Box className={classes.info}>{inputedSlippage != '' && !isNaN(inputedSlippage) ? inputedSlippage : slippage == 0 ? 0.5 : slippage == 1 ? 1 : 2}%</Box>
                </Box>
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 10}}>
                  <Box className={classes.sub}>Transaction speed:</Box>
                  <Box className={classes.info}>{speed == 0 ? 'Slow' : speed == 1 ? 'Fast' : 'Fastest'}</Box>
                </Box>
                <Box className={classes.divider}></Box>
                <Box style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginTop: 10}}>
                  <Box className={classes.total}>Total:</Box>
                  <Box className={classes.info}>{token?.symbol == 'ETH' ? `${getCorrectDecValue(Number(tokenAmount) + Number(getCorrectDecValue(gas * gasLimit, 18, 5, false)), 0, -1)} ETH` : `${tokenAmount} ${token.symbol} + ${getCorrectDecValue(gas * gasLimit, 18, 5)} ETH`}</Box>
                </Box>
                <Button 
                  variant="contained"
                  className={classes.preview_button}
                  onClick={() => send()}>
                    Send Now
                </Button>
              </Card>
            :
              loading ?
                <Card className={classes.step_card}>
                  <Box className={classes.back} onClick={() => setStep(1)}>
                    <BackIcon style={{marginRight: 12}}/>
                    Back
                  </Box>
                  <Box className={classes.error}>
                    Please Wait!
                  </Box>
                  <Box className={classes.success_description} style={{marginBottom: 100}}>
                    {`You're sending ${tokenAmount} ${token.symbol} ($${usdAmount}) to ${getReducedWalletAddress(recipient.address)}`}
                  </Box>
                </Card>
              :
              error ?
                <Card className={classes.step_card}>
                  <Box className={classes.back} onClick={() => setStep(1)}>
                    <BackIcon style={{marginRight: 12}}/>
                    Back
                  </Box>
                  <Box className={classes.error}>
                    Ouch!
                  </Box>
                  <Box className={classes.description}>
                    Something went wrong..
                  </Box>
                  <Button 
                    variant="contained"
                    className={classes.preview_button}
                    onClick={() => send()}>
                      Retry
                  </Button>
                </Card>
              :
                <Card className={classes.step_card}>
                  <Box style={{textAlign: 'center'}}>
                    <img src="/images/Logo.svg" className={classes.logo} />
                  </Box>
                  <Box className={classes.error}>
                    Success!
                  </Box>
                  <Box className={classes.success_description}>
                    {`You just send ${tokenAmount} ${token.symbol} ($${usdAmount}) to ${getReducedWalletAddress(recipient.address)}`}
                  </Box>
                  <Box className={classes.success_description}>
                    The transaction usually takes a few seconds
                  </Box>
                  <Button 
                    variant="contained"
                    className={classes.sendmore_button}
                    onClick={() => setStep(0)}>
                      Send More
                  </Button>
                  <Box style={{textAlign: 'center'}}>
                    <a href={`${NETWORK_TX_ENDPOINT[MAIN_NETWORK_ID]}${hash}`} target="_blank" rel="noopener noreferrer">
                      <Button size="medium" className={classes.cancel} >
                        View Transaction
                      </Button>
                    </a>
                  </Box>
                </Card>
          }
          
        </Grid>
        <Grid item md={3} xs={12}></Grid>
      </Grid>
      <TransactionSettingsModal 
        open={settingsModalOpen}
        handleClose={() => setSettingsModalOpen(false)}
        slippage={slippage}
        speed={speed}
        inputedSlippage={inputedSlippage}
        handleApply={handleApply}
      />
    </Box>
  );
};

export default Send;
