import { Box, Button, Dialog, IconButton,useMediaQuery } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import SearchInput from '../SearchInput';
import { CloseIcon } from './icons/icon';
import useStyles from './style';
import { confirmCorrectAddress } from '../../web3/contracts';
import { useSelector } from 'react-redux';
import { getReducedWalletAddress } from 'utils/services';

const RecipientSelectModal = props => {
  const { open, handleClose, handleSelectItem } = props;
  const classes = useStyles();
  const max375 = useMediaQuery('(max-width:375px)');
  const [searchStr, setSearchStr] = useState('');
  const addressList = useSelector(state => state.app.addressList);
  const [searchResult, setSearchResult] = useState([]);
  const [selected, setSelected] = useState(null);
  const EMPTY_IMAGE_URL =
  "https://villagesonmacarthur.com/wp-content/uploads/2020/12/Blank-Avatar.png";

  useEffect(() => {
    const filterRecipient = item => {
      setSelected(null);
      if (
        item.name.toLowerCase().search(searchStr.toLowerCase()) >= 0 ||
        item.address.toLowerCase() == searchStr.toLowerCase()
      )
        return true;
      return false;
    };
    let res = addressList.filter(filterRecipient);
    setSearchResult(res);
  }, [addressList, searchStr])

  function dealTokenForSendModal(address) {
    if (address && max375) {
      return `${address.substring(0, 12)}...${address.substring(30)}`
    }
    return address;
  }

  return (
    <Dialog onClose={handleClose} open={open} className={classes.dialog}>
      <Box className={classes.header}>
        <Box>Choose Recipient</Box>
        <Box className={classes.icon} onClick={handleClose}>
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
            className={classes.control_button}>
            <CloseIcon />
          </IconButton>
        </Box>
      </Box>
      <Box className={classes.searchbox}>
        <SearchInput
          placeholder="Phone, email, name, address or wallet"
          value={searchStr}
          onChange={e => setSearchStr(e.target.value)}
        />
      </Box>
      <Box className={classes.top_divider} />
      {
        (searchStr.length == 42 && confirmCorrectAddress(searchStr) || selected)&&
        <Button fullWidth={true} variant="contained" size="medium" className={classes.select_button} onClick={() => {handleSelectItem({address: searchStr || selected}); handleClose();}}>Choose this</Button>
      }
      <Box className={classes.token_list}>
        {searchResult.length == 0 ? (
          !confirmCorrectAddress(searchStr) ?
          <Box className={classes.error}>Invalid recipient address</Box>
          :
          <Box className={classes.no_data}>No Results found.</Box>
        ) : (
          searchResult.map((item, index) => (
            <Box key={index}>
              <Box
                style={selected == item.address ? {
                  display: 'flex',
                  justifyContent: 'space-between',
                  margin: '16px 0px',
                  cursor: 'pointer',
                  backgroundColor: '#F5F6FD',
                  borderRadius: 8
                } : {
                  display: 'flex',
                  justifyContent: 'space-between',
                  margin: '16px 0px',
                  cursor: 'pointer'
                }}
                onClick={() => setSelected(item.address)}>
                <Box style={{ display: 'flex', alignItems: 'center' }}>
                  <img
                    src={item?.urlImage || EMPTY_IMAGE_URL}
                    alt="Avatar"
                    className={classes.image}
                  />
                  <Box>
                    <Box className={classes.name}>{item.name}</Box>
                    <Box className={classes.address}>{dealTokenForSendModal(item.address)}</Box>
                  </Box>
                </Box>
              </Box>
            </Box>
          ))
        )}
      </Box>
    </Dialog>
  );
};

export default RecipientSelectModal;
