import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Box} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  root: {
    border: `1px solid #B2B7FF`,
    borderRadius: 12,
    padding: 16,
    backgroundColor: theme.palette.wallet_button_color,
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    height: 72,
    cursor: 'pointer',
    color: theme.palette.text_main,
    marginTop: 12
  },
  input: {
    marginLeft: theme.spacing(2),
    flexGrow: 1,
    fontSize: '24px',
    lineHeight: '16px',
    letterSpacing: '-0.05px',
    color: theme.palette.text_main,
    width: '150px'
  },
  tag: {
    color: theme.palette.text_sub,
    fontSize: 14,
    marginRight: theme.spacing(1)
  },
  connect_button: {
    padding: 0,
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    display: 'flex',
    justifyContent: 'space-between',
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none'
    }
  },
  imageBox: {
    width: 40,
    height: 40,
    marginRight: 10,
    backgroundColor: theme.palette.wallet_button_color,
    borderRadius: 9,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  image: {
    width: 20,
    height: 20,
  },
  title: {
    color: theme.palette.text_main,
    fontSize: 16
  },
  sub_title: {
    color: theme.palette.text_sub,
    fontSize: 16,
    marginLeft: 4
  },
  icon: {
    marginRight: 4.5,
    display: 'flex',
    alignItems: 'center'
  },
}));

const SendTokenBar = props => {
  const { children, tokenInfo, usdAmount, tokenAmount } = props;
  const classes = useStyles();

  return (
    <Box>
      <Box className={classes.root}>
        <Box style={{display: 'flex', alignItems: 'center'}}>
          <Box className={classes.imageBox}>
            <img src={tokenInfo?.logoURI} className={classes.image} />
          </Box>
          <Box>{tokenInfo?.symbol}</Box>
        </Box>
        <Box className={classes.icon}>
          <Box className={classes.title}>{tokenAmount}</Box>
          <Box className={classes.sub_title}>(${usdAmount})</Box>
        </Box>
      </Box>
    </Box>
  );
};

export default SendTokenBar;
