import React, { useEffect, useRef, useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Box, Input, Button, Grid, Avatar } from '@material-ui/core';
import { KeyboardArrowDown } from '@material-ui/icons';
import { getReducedWalletAddress } from 'utils/services';
import RecipientSelectModal from 'components/RecipientSelectModal';
import { CheckIcon } from 'components/Wallet/icons/icons';
import { LeftIcon } from 'views/Swap/icons/icon';

const useStyles = makeStyles(theme => ({
  root: {
    border: `1px solid ${theme.palette.blue_dark_border_color}`,
    borderRadius: 12,
    padding: 16,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.black_white,
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    height: 72,
    cursor: 'pointer',
  },
  input: {
    marginLeft: theme.spacing(2),
    flexGrow: 1,
    fontSize: '24px',
    lineHeight: '16px',
    letterSpacing: '-0.05px',
    color: theme.palette.text_main,
    width: '150px'
  },
  tag: {
    color: theme.palette.text_sub,
    fontSize: 14,
    marginRight: theme.spacing(1)
  },
  connect_button: {
    padding: 0,
    boxShadow: 'none',
    borderRadius: 9,
    border: `1px solid ${theme.palette.border_color}`,
    color: theme.palette.text_main,
    backgroundColor: theme.palette.wallet_button_color,
    textTransform: 'none',
    display: 'flex',
    justifyContent: 'space-between',
    '&:hover': {
      backgroundColor: theme.palette.wallet_button_color,
      boxShadow: 'none'
    }
  },
  imageBox: {
    width: 40,
    height: 40,
    marginRight: 10,
    backgroundColor: theme.palette.wallet_button_color,
    borderRadius: 9,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    marginRight: 10
  },
  image: {
    width: 20,
    height: 20,
  },
  title: {
  },
  icon: {
    marginRight: 4.5,
    display: 'flex',
    alignItems: 'center'
  },
}));

const RecipientInput = props => {
  const { children, recipientInfo, handleChange } = props;
  const classes = useStyles();
  const [open, setOpen] = useState(false);

  return (
    <Box>
      <Box className={classes.root} onClick={() => setOpen(true)}>
        {
          recipientInfo?.address ?
            recipientInfo?.name ? (
              <Box style={{display: 'flex', alignItems: 'center'}}>
                <Box className={classes.imageBox}>
                  <Avatar alt={recipientInfo?.name} src={recipientInfo?.avatar} className={classes.image} />
                </Box>
                <Box>
                  <Box className={classes.title}>{recipientInfo?.name}</Box>
                  <Box className={classes.address}>{getReducedWalletAddress(recipientInfo?.address || '')}</Box>
                </Box>
              </Box>
            ) : (
              <Box style={{display: 'flex', alignItems: 'center'}}>
                <CheckIcon style={{marginRight: 10}}/>
                <Box className={classes.title}>{getReducedWalletAddress(recipientInfo?.address || '')}</Box>
              </Box>
            )
          :
          <Box className={classes.title}>Choose Recipient</Box>
        }
        <Box className={classes.icon}>
          <LeftIcon />
        </Box>
      </Box>
      <RecipientSelectModal
        open={open}
        handleClose={() => setOpen(false)}
        handleSelectItem={handleChange}
      />
    </Box>
  );
};

export default RecipientInput;
